function SingleNFT({match}) {
  const classes = useStyles();
  const history = useHistory();
  const { asset_contract_address, token_id } = match.params;

  const NftList = useSelector(state => state.app.nftList);
  const [priceData, setPriceData] =useState(NftList[token_id])
  const [price, setPrice] = useState('')
  const [priceForHistory, setPriceForHistory] = useState('')
  const [lastPrice, setLastPrice] = useState(+price > +priceForHistory ? +price : +priceForHistory);

  const [nft, setNft] = useState(singleNFTModel);
  const [priceHistoryData, setPriceHistoryData] = useState();
  const [rarity, setRarity] = useState('');
  
  // Retrieve an asset 
  useEffect(() => {
    try {
      getNFTsAssets(`https://api.opensea.io/api/v1/asset/${asset_contract_address}/${token_id}`, (err, data)=> {
        if (err) return
        setNft(data);
      })
    } catch (error) {
      console.log('error', error)
    }
    
  }, [])

  // Fetching the price offer of an asset
  useEffect(() => {
    try {
      getNFTsAssets(`https://api.opensea.io/api/v1/asset/${asset_contract_address}/${token_id}/offers`, (err, data)=> {
        if (err) return
        if (data.offers.length === 0) return
        setPriceHistoryData(data.offers);
        setPriceForHistory(data?.offers.length > 0 ? data?.offers[0]?.base_price : 0);
        setLastPrice(+price > +data?.offers[0]?.base_price ? price : data?.offers[0]?.base_price)
      })
    } catch (error) {
      console.log('error', error)
    }
    
  }, [])

  // Displaying price for various events to retrieve an asset
  useEffect(() => {
    const displayingPrice = () => {
      if(!nft.last_sale) return
      if (nft.last_sale.event_type === 'created'){
        setPrice(nft.last_sale.starting_price)
      } else if (nft.last_sale.event_type === 'successful') {
        setPrice(nft.last_sale.total_price)
      } else if (nft.last_sale.event_type === 'cancelled') {
        setPrice(nft.last_sale.total_price ?? nft.last_sale.ending_price)
      } else if (nft.last_sale.event_type === 'bid_entered' ) {
        setPrice(nft.last_sale.bid_amount)
      } else if (nft.last_sale.event_type === 'bid_withdrawn') {
        setPrice(nft.last_sale.total_price ?? nft.last_sale.starting_price)
      } else if (nft.last_sale.event_type === 'transfer') {
        setPrice("")
      } else if (nft.last_sale.event_type === 'offer_entered') {
        setPrice(nft.last_sale.bid_amount)
      } else if (nft.last_sale.event_type === 'approve') {
        setPrice("")
      }

      setLastPrice(+price > +priceForHistory ? price : priceForHistory)
    }
    displayingPrice()
  },[nft])

  const goBack = () => {
    history.goBack(1)
  }

  // Price History Data 
  const [selectedIndex, setSelectedIndex] = useState(5);
  const [data, setData] = useState([]);
  const [minPrice, setMinPrice] = useState(0);
  const [maxPrice, setMaxPrice] = useState(10);
  
  useEffect(() => {
    let offer_base_price = Math.round((Number(Web3.utils.fromWei(price)) + Number.EPSILON) * 100) / 100;

    const chartData = priceHistoryData?.reverse().map(item => {
      offer_base_price = Math.round((Number(Web3.utils.fromWei(item.base_price)) + Number.EPSILON) * 100) / 100

      const month = new Date(item.closing_date).getMonth().toString();

      const day = new Date(item.closing_date).getDate().toString();

      return {
        date: `${month}/${day}`,
        price: offer_base_price,
        amt: 2,
      }
    })
    
    if (chartData?.length) {
      chartData.push({
        name: ``,
        price: Math.round((Number(Web3.utils.fromWei(lastPrice? lastPrice : price)) + Number.EPSILON) * 100) / 100,
        amt: 2,
      })
      setData(chartData)
      setMaxPrice(Math.ceil(Number(offer_base_price)))
    } else {
      const chartData = [
        {
          name: 'Page A',
          price: 0,
          amt: 2,
        }
        
      ];
      chartData.push({
        name: ``,
        price: Math.round((Number(Web3.utils.fromWei(lastPrice? lastPrice : price)) + Number.EPSILON) * 100) / 100,
        amt: 2,
      })
      setData(chartData)
      setMaxPrice(Math.ceil(Number(offer_base_price)))
    }
  },[priceHistoryData,price])

  // Rarity Score Functionality
  useEffect(() => {
    const rarityOfTraitValue = nft.traits.map(item => {
      const traitRarity = Number(+nft.collection.stats.total_supply/+item.trait_count)
      return traitRarity
    })

    const sumOfTraitsRarity = rarityOfTraitValue.reduce((previousValue,currentValue) => previousValue + currentValue,0)

    setRarity(sumOfTraitsRarity.toFixed(2));
  },[nft])

  // Accordion Expand Functionality 
  const [expanded, setExpanded] = useState(true);
  const [expandedPH, setExpandedPH] = useState(true);

  const handleChangeExpand = (panel) => (event, isExpanded) => {
    setExpanded(panel);
  };

  const handleChangePH = (panel) => (event, isExpanded) => {
    setExpandedPH(panel);
  };

  // Lowest Price & Last Price
  const [isLowestPrice, setIsLowestPrice] = useState(true);
  const [isLastPrice, setIsLastPrice] = useState(false);

  const handleToggle = () => {
    setIsLowestPrice(!isLowestPrice);
    setIsLastPrice(!isLastPrice);
  }

  useEffect(() => {
    if((isLowestPrice && nft.last_sale)) {
      if (nft.last_sale.event_type === 'created'){
        setPrice(nft.last_sale.starting_price < priceForHistory ? nft.last_sale.starting_price : priceForHistory)
      } else {
        setPrice(+price < +priceForHistory ? price : priceForHistory)
      }
    } else if (isLastPrice && nft.last_sale) {
      if (nft.last_sale.event_type === 'successful') {
        setPrice(nft.last_sale.total_price)
        setLastPrice(nft.last_sale.total_price)
      } else {
        setPrice(+price > +priceForHistory ? price : priceForHistory)
  
      }
    }
  }, [isLowestPrice, isLastPrice])

    // prce comparison
    // const priceComparison = useCallback(() => {
    //   if(!nft.last_sale) return
    //   setLastPrice(price > priceForHistory ? price : priceForHistory)
    //   console.log('price', price, priceForHistory)
    // }, [nft, price, priceForHistory])
    
    useEffect(() => {
      setLastPrice(+price > +priceForHistory ? price : priceForHistory)
    },[price, priceForHistory])
    

  return (
    <Grid container>
      <Grid item xs={12} sm={12} md={12}>
        <Typography 
          className={classes.buttonText16} 
          style={{ color: "#8087ED", display: "inline-flex", alignItems: "center", marginBottom: "1.5rem", cursor: "pointer" }}
          onClick={goBack}
        > 
          <ChevronLeft />   
          <span style={{marginLeft: ".7rem"}}> Back to NFT’s page </span> 
        </Typography>
      </Grid>
      <Grid item xs={12} sm={5}>
        <Box 
          component="img" 
          alt="" 
          src={nft?.image_url}
          className={classes.nftImg}
        />
        <Box className={classes.nftInfo}>
          <Box 
            className={classes.cardTitleGroup}
            style={{
              display: "flex", 
              alignItems: "center", 
              justifyContent: '',
              marginTop: "1rem",
              marginBottom: "1rem"
            }}
          >
            <Typography 
              className={classes.title24}
            > {nft?.collection && nft?.collection.name} 
            </Typography>
          </Box>
          <Typography className={classes.subtitle16}> {nft?.collection && nft?.collection.description} </Typography>

          <Box className={classes.marketPlace}>
            <Link className={classes.text16} href={nft?.permalink}>
              Opensea <Box component="img" alt="share" src={share} style={{marginLeft: "5px", marginRight: "1.2rem"}}/>
            </Link>
            <Link className={classes.text16} href={`https://rarible.com/token/${asset_contract_address}:${token_id}`}>
              Rarible <Box component="img" alt="share" src={share} style={{marginLeft: "5px"}} />
            </Link>
          </Box>
        </Box>
      </Grid>
      <Grid item xs={12} sm={7}>
        
        <Box className={classes.nftHeader}>
          <Box style={{background: "rgba(178, 183, 255, 0.2)", padding: "5px", width: "atuo"}}>
            <Button 
              className={isLowestPrice ? classes.priceFilteringBtn : ""} 
              onClick={handleToggle}
            >
              <span className={classes.textItem14}> Lowest price </span>
            </Button>
            <Button
              className={isLastPrice ? classes.priceFilteringBtn : ""}
              onClick={handleToggle}
            >
              <span className={classes.textItem14}> Last price </span>
            </Button>
          </Box>
        </Box>

        <Box style={{margin:"1rem 0"}}>
          <Typography className={classes.title24}>
            {`${nft?.name ?? '#'+token_id}`}
          </Typography>
        </Box>

        <Box
          style={{display: "flex", alignItems: "center", justifyContent: 'space-between',margin: "0 0 1rem 0"}}
        > 
          <Box
            style={{display: "flex", alignItems: "center", justifyContent: 'flex-start',margin: "0 0 1rem 0"}}
          > 
            <Typography style={{marginRight: ".5rem"}}> 
              <span className={classes.regularText14} style={{marginRight: ".5rem"}}>
                Price: 
              </span> 
              <span className={classes.buttonText16}>
                { isLowestPrice ? 
                    (+price < +priceForHistory ? Web3.utils.fromWei(price) : Web3.utils.fromWei(priceForHistory)) 
                  : 
                    isLastPrice ? (+price > +priceForHistory ? Web3.utils.fromWei(price) : Web3.utils.fromWei(priceForHistory))
                  : '0' 
                } ETH
              </span> 
            </Typography> 
            <Button className={classes.itemBtn} style={{marginTop: "-5px"}}>
              <span className={classes.regularText14}> 
                Single Item
              </span>
            </Button>
          </Box>
          <Box
            style={{display: "flex", alignItems: "center", justifyContent: 'flex-start',margin: "0 0 .5rem 0"}}
          > 
            <Typography style={{marginRight: ".5rem"}}> 
              <span className={classes.regularText14} style={{marginRight: ".5rem"}}>
                Share: 
              </span> 
            </Typography> 
            <Box 
              component="img" 
              alt="discord"
              src={discord}
              style={{marginRight: ".2rem"}}  
            />
            <Box 
              component="img" 
              alt="twitter"
              src={twitter}
              style={{marginRight: ".2rem"}}  
            />
            <Box 
              component="img" 
              alt="linkedin"
              src={linkedin}
              style={{marginRight: ".2rem"}}  
            />
            <Box 
              component="img" 
              alt="facebook"
              src={facebook}
              style={{marginRight: ".2rem"}}  
            />

          </Box>
        </Box>

        <Box
          className={classes.rarityBox}
          style={{display: "flex", alignItems: "center", justifyContent: 'space-between',margin: "0 0 1rem 0"}}
        > 
          <Box>
            <Typography className={classes.title20}>Rarity score:</Typography>
            <Typography className={classes.caption12}> rarity.tools experimental </Typography>
          </Box>
          <Typography className={classes.title20}> {rarity === "Infinity" ?"": rarity} </Typography>
        </Box>

        <Accordion className={classes.accordion} expanded={expanded} onChange={handleChangeExpand(!expanded)}>
          <AccordionSummary
            expandIcon={<ExpandMoreIcon style={{color:"#3F4271"}} />}
            aria-controls="panel1a-content"
            id="panel1a-header"
          >
            <Typography className={classes.title20}>Attributes</Typography>
          </AccordionSummary>

          <AccordionDetails style={{display: 'flex',flexWrap: 'wrap'}}>
            {
              nft?.traits && nft?.traits.map((trait,index) => (
                <Box className={classes.attributeBox} key={trait.trait_type+index}>
                  <Typography className={classes.caption12} style={{textTransform: "uppercase"}}>
                    {trait.trait_type}
                  </Typography>
                  <Typography className={classes.buttonText16}>
                    {trait.value}
                  </Typography>
                </Box>
              ))
            }
            
          </AccordionDetails>
        </Accordion>

        <Accordion className={classes.accordion} expanded={expandedPH} onChange={handleChangePH(!expandedPH)}>
          <AccordionSummary
            expandIcon={<ExpandMoreIcon style={{ color: "#3F4271" }} />}
            aria-controls="panel1a-content"
            id="panel1a-header"
          >
            <Typography className={classes.title20}>Price History</Typography>
          </AccordionSummary>

          <AccordionDetails style={{ display: 'flex', flexWrap: 'wrap' }}>
            <Box className={classes.cardChart}>
              <NormalCard>

                <div style={{ height: 300 }}>
                  <ResponsiveContainer>
                    <AreaChart
                      width={500}
                      height={400}
                      data={data}
                      margin={{
                        top: 10,
                        right: 30,
                        left: 0,
                        bottom: 0
                      }}>
                      <defs>
                        <linearGradient id="colorUv" x1="0" y1="0" x2="0" y2="1">
                          <stop offset="0%" stopColor="#E0E3FF" stopOpacity={1} />
                          <stop offset="100%" stopColor="#E0E3FF" stopOpacity={0} />
                        </linearGradient>
                      </defs>
                      <CartesianGrid vertical={false} />
                      <XAxis dataKey="none" axisLine={false} tickLine={false} />
                      <YAxis
                        dataKey="amt"
                        axisLine={false}
                        tickLine={false}
                        domain={[minPrice, maxPrice]}
                      />
                      <Tooltip />
                      <Area
                        type="monotone"
                        dataKey="price"
                        stroke="#B2B7FF"
                        fill="#E0E3FF"
                        activeDot={{ stroke: '#8087ED', strokeWidth: 8, r: 8 }}
                      />
                    </AreaChart>
                  </ResponsiveContainer>
                </div>
                <List
                  className="tableScrollBar"
                  style={{ display: 'flex', overflowX: 'auto' }}>
                  <ListItem
                    key="1D"
                    selected={selectedIndex === 0}
                    className={classes.button}
                    onClick={() => setSelectedIndex(0)}>
                    1H
                  </ListItem>
                  <ListItem
                    key="1H"
                    selected={selectedIndex === 1}
                    className={classes.button}
                    onClick={() => setSelectedIndex(1)}>
                    1D
                  </ListItem>
                  <ListItem
                    key="1W"
                    selected={selectedIndex === 2}
                    className={classes.button}
                    onClick={() => setSelectedIndex(2)}>
                    1W
                  </ListItem>
                  <ListItem
                    key="1M"
                    selected={selectedIndex === 3}
                    className={classes.button}
                    onClick={() => setSelectedIndex(3)}>
                    1M
                  </ListItem>
                  <ListItem
                    key="1Y"
                    selected={selectedIndex === 4}
                    className={classes.button}
                    onClick={() => setSelectedIndex(4)}>
                    1Y
                  </ListItem>
                  <ListItem
                    key="ALL"
                    selected={selectedIndex === 5}
                    className={classes.button}
                    onClick={() => setSelectedIndex(5)}>
                    ALL
                  </ListItem>
                </List>
              </NormalCard>
            </Box>
          </AccordionDetails>
        </Accordion>
      </Grid>
    </Grid>
  )
}

export default SingleNFT